html,
body {
  margin: 0;
  padding: 0;
}

body {
  font: 16px 'Helvetica Neue', Helvetica, Arial;
  font-weight: 300;
  line-height: 1.5;
  width: auto;
  background: #fafafa;
  color: #353535;
  font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

h2 {
  margin: 10px 0;
  font-size: 24px;
}

a,
.link {
  color: #DE0000;
  font-weight: 500;
  cursor: pointer;
  text-decoration: none;
}


a:hover,
.link:hover {
  color: #431919;
  text-decoration: underline;
}

ul{
  list-style: none;
  padding: 0;
}

.todos{
  background-color:#fff;
  border: 1px solid #000;
  border-radius: 3px;
  padding: 0.5em 1.5em;
  margin-bottom: 0.5em;
}

.hidden{
  display: none;
}

.counter{
  font-size: 0.6em;
  float: right;
  margin-left: 0.2em;
  margin-top: -0.3em;
  padding: 0 0.4em;
  border: 1px solid #DE0000;
  background-color: #DE0000;
  color: white;
  border-radius: 50%;
}

footer{
  li{
    display: inline-block;
    margin-right: 1em;
  }
}

section{
  margin: 0 auto;
  width: 80%;
  h1, footer{
    text-align: center;
  }
  .todo-text{
    width: 80%;
    margin: 0 auto;
    background-color: transparent;
    border: 0;
    border-bottom: 1px solid #000;
    height: 1.8em;
    font-size: 1.2em;
    margin-bottom: 1em;
    outline: none;
    padding: 0;
    &:focus{
      border: 1px solid teal;
      border-radius: 3px;
    }
  }
  button{
    background-color: transparent;
    border: none;
    border: 1px solid #25C425;
    border-radius: 3px;
    font-size: 1em;
    font-weight: 300;
    height: 2em;
    margin-left: 0.5em;
  }
}
.active{
  text-decoration: underline;
}
.complete{
  background-color: #25C425;
  color: white;
  border-color: #25C425;
}